.comment {
    max-width: 100%;
    padding: 12px 24px;
    border: 1px solid rgba(0,0,0,1);
    & > .comment__header {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 20px;
        & > h3 {
            display: block;
            width: 50%;
            height: 2rem;
            border-radius: 3px;
            background: #eee;
            background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
            background-size: 200% 100%;
            animation: 1.5s shine linear infinite;
        }
        & > .comment__email {
            display: block;
            width: 10%;
            height: 1rem;
            border-radius: 3px;
            background: #eee;
            background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
            background-size: 200% 100%;
            animation: 1.5s shine linear infinite;
        }
    }
    & > p {
        display: block;
        width: 100%;
        height: 6rem;
        border-radius: 3px;
        background: #eee;
        background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
        background-size: 200% 100%;
        animation: 1.5s shine linear infinite;
    }
}

@keyframes shine {
    to {
      background-position-x: -200%;
    }
}