.skeleton {
    max-width: 414px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 13px 35px 18px 30px;
}
.skeleton__header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.skeleton__title {
    display: block;
    width: 50%;
    height: 1.5rem;
    border-radius: 3px;
    background: #eee;
    background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
}
.skeleton__date {
    display: block;
    width: 10%;
    height: 1.5rem;
    border-radius: 3px;
    background: #eee;
    background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
}
.skeleton__text {
    display: block;
    width: 100%;
    height: 6rem;
    border-radius: 3px;
    background: #eee;
    background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
    background-size: 200% 100%;
    animation: 1.5s shine linear infinite;
}

@keyframes shine {
    to {
      background-position-x: -200%;
    }
}

@media screen and (max-width: 430px) {
    .skeleton {
        padding: 18px 20px 46px 20px;
    }
    .content__title {
        text-align: center;
    }
}