.preview {
    position: relative;
    height: 440px;
    box-shadow: inset 0 -50px 30px rgba(0,0,0,.9);
}
.preview__shadow{
    margin-bottom: 25px;
    width: 90%;
    border-radius: 100%;
    background: linear-gradient(
        rgba(0, 0, 0, 1),
        rgba(0, 0, 0, 0)
    );
}
.preview__shadow_inset{
    width: 55%;
    margin: 0 auto;
    border-radius: 100%;
    background: linear-gradient(
        rgba(0, 0, 0, 1),
        rgba(0, 0, 0, 0)
    );
}
.preview__info {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.preview__info h1{
    text-align: center;
    font-family: 'AlegreyaSansSC';
    font-weight: 700;
    font-size: 50px;
    line-height: 60px;
    color: white;
}
.preview__datetime {
    display: block;
    width: 100%;
    text-align: center;
    font-family: 'AlegreyaSansSC';
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    color: white;
}
.preview__navigation_bottom {
    position: absolute;
    bottom: 48px;
    left: 0;
    width: 100%;
}
.preview__navigation_bottom > div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.preview__button_move {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black ;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(13, 30, 75, 1);
    font-weight: 800;
    font-size: 24px;
    cursor: pointer;
}
.preview__button_prev_disable {
    border: 1px solid white;
    background: linear-gradient(
        rgba(0, 0, 0, 1),
        rgba(0, 0, 0, 0.55),
        rgba(0, 0, 0, 0.14),
        rgba(0, 0, 0, 0)
    );
    color: rgba(255, 255, 255, 1);
}
.preview__button {
    width: 166px;
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 1);
    text-decoration: none;
    text-align: center;
    font-family: 'Proxima';
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 1);
}
.preview__slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    z-index: -1;
}

@media screen and (max-width: 845px) {
    .preview__info h1 {
        font-size: 40px;
    }
    .preview__datetime{
        font-size: 24px;
    }
}
@media screen and (max-width:420px) {
    .preview__info h1 {
        font-size: 36px;
        line-height: 45px;
    }
    .preview__datetime {
        font-size: 21px;
    }
    .preview__button{
        width: auto;
    }
}