.post {
    margin-top: 2rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    & > h1 {
        display: block;
        width: 100%;
        height: 2rem;
        border-radius: 3px;
        background: #eee;
        background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
        background-size: 200% 100%;
        animation: 1.5s shine linear infinite;
    }
    & > p {
        display: block;
        width: 100%;
        height: 6rem;
        border-radius: 3px;
        background: #eee;
        background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
        background-size: 200% 100%;
        animation: 1.5s shine linear infinite;
    }
}

@keyframes shine {
    to {
      background-position-x: -200%;
    }
}