.contact {
    height: fit-content;
    border: 1px solid rgba(218, 218, 218, 1);
}
.contact__wrapper {
    margin: 10px 23px 0px 21px;
    display: flex;
    flex-direction: column;
}
.contact__title {
    margin-bottom: 8px;
    font-family: 'OpenSans-Semi-Bold';
    font-weight: 600;
    font-size: 18px;
    line-height: 40px;
}
.contact__message {
    padding: 10px 20px;
    height: 112px;
}
.contact__button {
    margin-top: 15px;
    margin-bottom: 23px;
    width: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: flex-end;
    padding: 10px 20px;
    border: 1px solid rgba(13, 30, 75, 1);
    background-color: rgba(16, 16, 16, 1);
    text-decoration: none;
    font-family: 'OpenSans-Semi-Bold';
    font-size: 11px;
    font-weight: 600;
    line-height: 15px;
    color: rgba(255, 255, 255, 1);
}