.group {
    margin-bottom: 38px;
}
.group > div {
    display: flex;
    gap: 28px;
}
.group > div > h2 {
    font-family: 'AlegreyaSansSC';
    font-size: 35px;
    font-weight: 500;
    line-height: 40px;
    white-space: nowrap;
}
.group__description {
    margin-top: 8px;
    font-family: 'OpenSans-Semi-Bold';
    font-size: 16px;
    line-height: 24px;
    color: rgba(147, 147, 147, 1);
}

@media screen and (max-width:500px) {
    .group > div {
        flex-direction: column;
        gap: 0;
    }
}